import React from 'react';
//import { StaticImage, GatsbyImage, getImage } from "gatsby-plugin-image"
import { graphql } from 'gatsby';
import { MDXRenderer } from 'gatsby-plugin-mdx';

import Layout from '../components/layout';
//import Img from 'gatsby-image';
import './blogPostTemplate.css';


export default ({ data }) => {
  const { frontmatter, body, fields } = data.mdx;
  // console.log(frontmatter.featuredImage);
  return (
    <Layout>
      <h1 className="text-secondary">{frontmatter.title}</h1>
      <p>{frontmatter.date}</p>
      <p>{'econolabs.github.io' + fields.slug}</p>
      <img
        src={frontmatter.featuredImage}
        style={{maxWidth:"640", height:'auto' }}
        className="fade-in"
       // maxWidth="640"
       // height="390"
      />
{/* 
      <StaticImage
        src="https://images.unsplash.com/photo-1596638787647-904d822d751e?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=640&q=80"
        alt="A dinosaur"
        placeholder="blurred"
        layout="fixed"
        width={640}
        height={390}
      />

      <RenderSmoothImage src={frontmatter.featuredImage} alt={frontmatter.title} />  
      <BlogPost data={frontmatter.featuredImage} /> */}
      <MDXRenderer>{body}</MDXRenderer>
    </Layout>
  );
};

export const query = graphql`
  query PostsBySlug($slug: String!) {
    mdx(fields: { slug: { eq: $slug } }) {
      body
      frontmatter {
        title
        date(formatString: "YYYY MMMM Do")
        featuredImage 
      }
      fields {
        slug
      }
    }
  }
`;

